import { Component, OnInit } from '@angular/core';

import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from '../blog.service';
import { BlogHttpService } from "../blog-http.service";

import { ToastrService } from 'ngx-toastr';
import {Location} from '@angular/common';



@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
  providers:[Location],
})
export class BlogViewComponent implements OnInit {

  public currentBlog;

  constructor(private _route: ActivatedRoute, private router: Router, public blogService: BlogService, public blogHttpService: BlogHttpService, private toastr: ToastrService,private location:Location) {
    console.log("constructer is called");
  }

  ngOnInit() {
    console.log("ngOnINIT");
    let myBlogId = this._route.snapshot.paramMap.get('blogId');

    console.log(myBlogId);

    this.currentBlog = this.blogService.getSingleBlogInformation(myBlogId);
    console.log(this.currentBlog);

    this.blogHttpService.getSingleBlogInformation(myBlogId).subscribe(
      data => {
        console.log(data);
        this.currentBlog = data["data"];
      },
      error => {
        console.log("Sone error occured");
        console.log(error.errorMessage);
      }
    )
  }

  public deleteThisBlog(): any {
    this.blogHttpService.deleteBlog(this.currentBlog.blogId).subscribe(
      data => {
        console.log(data);
        this.toastr.success("Blog Deleted", "Success");

        setTimeout(() => {
          this.router.navigate(['/home']);
        }, 1000);
      },


      error => {
        console.log("Sone error occured");
        console.log(error.errorMessage);
      }




    )
  }

  public goBackToPreviosPage():any{
    this.location.back();
  }



}

