import { Component, OnInit } from '@angular/core';
import { BlogHttpService } from "../blog-http.service";

import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-blog-edit',
  templateUrl: './blog-edit.component.html',
  styleUrls: ['./blog-edit.component.css']
})
export class BlogEditComponent implements OnInit {
  currentBlog: any;

  constructor(private _route: ActivatedRoute, private router: Router, public blogHttpService: BlogHttpService, private toastr: ToastrService) {
    console.log("Construcutor for edit component is called");
   }



  ngOnInit() {
    console.log("ngOnINIT");
    let myBlogId = this._route.snapshot.paramMap.get('blogId');

    console.log(myBlogId);

    this.currentBlog = this.blogHttpService.getSingleBlogInformation(myBlogId);
    console.log(this.currentBlog);

    this.blogHttpService.getSingleBlogInformation(myBlogId).subscribe(
      data => {
        console.log(data);
        this.currentBlog = data["data"];
      },
      error => {
        console.log("Sone error occured");
        console.log(error.errorMessage);
      }
    )
  }

  public editThisBlog():any{
    console.log("edit this method is called");
    this.blogHttpService.editBlog(this.currentBlog.blogId,this.currentBlog).subscribe(
      data=>{
        console.log(data);
        this.toastr.success('Blog Deleted Successfully','Success');

        setTimeout(()=>{
          this.router.navigate(['/blog',this.currentBlog.blogId]);
        },1000)
      },

      error=>{
        console.log("Some error occured");
        console.log(error.errorMessage);
      }
    )
  }



}
